import { useEffect, useState } from "react"

import { useWeb3Context } from "../../../hooks/web3Context";
import {Trade} from '../../../container/trade';
import {AMM} from "../../../container/amm";
import { DEFAULD_NETWORK } from "../../../constants/blockchain";
import { PositionDraft } from "@perp/sdk-curie";

export function usePositionSize() {
  const { connected, provider, providerChainID } = useWeb3Context();  
	const [oppositeAmount, setOppositeAmount] = useState('');
	const [entryPrice, setEntryPrice] = useState('');
	const [tradingFee, setTradingFee] = useState('');
	const [priceImpact, setPriceImpact] = useState('');
	const [buyingPower, setBuyingPower] = useState('');	
	const {perpetualObject} = Trade.useContainer();
	const [newPositionDraft, setNewPositionDraft] = useState<PositionDraft<string> | undefined>();
	const [isCalculating, setIsCalculating] = useState<boolean>(false)
	
	let _newPositionDraft: PositionDraft<string> | undefined

	const {side} = Trade.useContainer();
	const {marketticker} = AMM.useContainer();
	const {collateral} = Trade.useContainer();
	const {slippage} = Trade.useContainer();
	const isAmountInputBase = false;
	let tickerSymbol = marketticker;
  let amountInput = collateral;				
	
	useEffect(() => {        
		async function updatePositionByUserControl() {
			if( connected && providerChainID === DEFAULD_NETWORK ) {						
				if(collateral === null) {
					setOppositeAmount('');
					return
				}
				if(collateral.eq(0)) {
					setOppositeAmount('0');
					return 
				}
				setIsCalculating(true);
				const hasconnected = perpetualObject.hasConnected();  
				if(hasconnected === false) {
					await perpetualObject.init();
					await perpetualObject.connect({ signer: provider.getSigner() });	
				}						
				_newPositionDraft = perpetualObject.clearingHouse?.createPositionDraft({
					tickerSymbol,
					side,
					amountInput,
					isAmountInputBase
				});	
				console.log("NewPosition::", _newPositionDraft);	
				if(_newPositionDraft !== undefined && newPositionDraft === undefined) {
					setEntryPrice((await _newPositionDraft!.getEntryPrice()).toFixed(2));
					setBuyingPower((await _newPositionDraft!.getBuyingPower()).toFixed(4));					
				}		
				setTradingFee((await _newPositionDraft!.getTransactionFee()).toFixed(8));
				setPriceImpact((await _newPositionDraft!.getPriceImpact()).toFixed(5));				
				const oppositeAmountBound = await _newPositionDraft!.getOppositeAmountBound(slippage); 
				setOppositeAmount(oppositeAmountBound!.toFixed(7));		
				setNewPositionDraft(_newPositionDraft);	
				setIsCalculating(false);			
			}
		}
		updatePositionByUserControl();				
	}, [connected, providerChainID, marketticker, side, collateral, slippage]); 	
	return { oppositeAmount, isCalculating, newPositionDraft, entryPrice, tradingFee, priceImpact, buyingPower };
}