import { createSlice, PayloadAction } from "@reduxjs/toolkit";

export interface IPositionSlice {
	isPositionOpened: boolean;
	isPositionUpdated: boolean;
}

const initialState: IPositionSlice = {
	isPositionOpened: false,
	isPositionUpdated: false
}

const positionsSlice = createSlice({
	name: "positions",
	initialState,
	reducers: {
		open(state)	{
			state.isPositionOpened = true;
		},		
		close(state) {
			state.isPositionOpened = false;
		},
		update(state) {
			state.isPositionUpdated = !state.isPositionUpdated;
		}
	}    
});

export const { open, close, update } = positionsSlice.actions;

export default positionsSlice.reducer;